<template>
    <div style="padding-top:20px;">
        <el-form :model="form" ref="form" label-width="100px">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="活动id">
                        <el-input v-model="form.number"></el-input>
                    </el-form-item>
                    <el-form-item label="手机号">
                        <el-input v-model="form.mobile"></el-input>
                    </el-form-item>
                    <!-- <el-form-item label="用户id">
                <el-input v-model="form.userId"></el-input>
                    </el-form-item>-->
                    <el-form-item label="抽奖结果">
                        <el-input v-model="form.result" type="textarea" :rows="4"></el-input>
                    </el-form-item>
                    <el-form-item prop="name">
                        <el-button type="primary" @click="submit1">抽奖</el-button>
                        <el-button type="primary" @click="submit2">申请抽奖资格</el-button>
                        <el-button type="primary" @click="submit3">申请抽奖资格和抽奖</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
import api from "@/api";
import { UPLOAD_DOMAIN } from "@/api/config";
export default {
    data() {
        return {
            form: {
                number: "",
                mobile: "",
                // userId: "",
                result: ""
            },
            domain: UPLOAD_DOMAIN
        };
    },
    methods: {
        submit1() {
            let params = {
                number: this.form.number,
                mobile: this.form.mobile
            };
            api.post("getLotteryResult", params, {
                baseURL: this.domain + "api"
            }).then(res => {
                this.form.result = res.data;
            });
        },
        submit2() {
            let params = {
                number: this.form.number,
                mobile: this.form.mobile
            };
            api.post("lotteryQualificationsApply", params, {
                baseURL: this.domain + "api"
            }).then(res => {
                this.form.result = res.data;
            });
        },
        submit3() {
            let params = {
                number: this.form.number,
                mobile: this.form.mobile
            };
            api.post("qualificationsApplyAndGetLotteryResult", params, {
                baseURL: this.domain + "api"
            }).then(res => {
                this.form.result = res.data;
            });
        }
    }
};
</script>



